import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import $ from 'jquery';

let trial = "";

class ClinicalTrial extends Component {

  constructor() {
    super();

    this.state = {
      trial: ""
    };
  }

  componentDidMount() {
    let { id } = this.props.params;
    let queryUrl =
      `http://localhost:3000/clinical-trial/${id}`;
    this.serverRequest = $.getJSON(queryUrl, (data) => {
      this.setState({
        trial: JSON.stringify(data)
      });
    });
  }

  componentWillUnmount() {
    this.serverRequest.abort();
  }

  render() {
    let { id } = this.props.params;
    return (
      <div>
        <div>{id} {this.state.trial}</div>
        <Link to="/">home</Link>
      </div>
    );
  }
}

export default ClinicalTrial;



